import { useParams, Link } from "react-router-dom";
import { useState, useEffect } from "react";
import api from '../api'
import Fiche from '../components/Fiche'

const Edit = () => {
  const { rowIndex } = useParams();
  const [data, setData] = useState(api.defaultState);
  const [lists, setList] = useState(api.defaultList);
  const [bookCount, setBookCount] = useState(0);

  useEffect(() => {
    const getData = async () => {
      setData(await api.getBook(rowIndex))
    };
    const getList = async (ame) => setList(await api.getLists())
    const getBookCount = async () => setBookCount(await api.getBookCount())
    getData();
    getBookCount();
    getList();
  }, [rowIndex]);

  const handleChange = (e) =>
    setData({ ...data, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {

      api.saveBook(rowIndex, data)

      //history.replace("/");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <form style={{ maxWidth: 500, margin: "auto" }} onSubmit={handleSubmit}>

      <div className="row">
        <div className="col">
          <Link 
            to={`/edit/${parseInt(rowIndex) - 1}`}
            className={`btn btn-primary ${rowIndex === "0" ? "disabled" : ""}`}>
            &lt;&lt;
          </Link>
        </div>
        <h1 className="col-8 text-muted text-center">Modifier un livre</h1>
        <div className="col">
          <Link 
            to={`/edit/${parseInt(rowIndex) + 1}`} 
            className={`btn btn-primary ${parseInt(rowIndex)+1 === bookCount ? "disabled" : ""}`}>
            &gt;&gt;
          </Link>
        </div>
      </div>

      <Fiche props={{ data: data, lists: lists, handleChange }} />

      <div className="text-center">
        <button className="btn btn-primary">Update</button>
      </div>
    </form>
  );
};

export default Edit;
