import { Link, useHistory } from "react-router-dom";
import { useEffect, useState } from "react";
import api from '../api'
import { usePersistedState  } from "../utils/usePersistedState";

const Home = () => {
  const [data, setData] = useState();
  const [filter, setFilter] = usePersistedState('filter', null);
  const [displayMode, setDisplayMode] = usePersistedState('displayMode', 'table')

  const getData = async () => {
    const books = await api.getBooks()
    setData(books)
    //setData(Object.keys(data).map((key) => data[key]));
  };

  useEffect(() => {
    getData();
  }, []);

  const history = useHistory()

  const handleDelete = async (rowIndex) => {
    try {
      const res = await fetch(
        `https://sheet.best/api/sheets/bff990d0-8ada-43e9-97eb-0ad668bb19ec/${rowIndex}`,
        {
          method: "DELETE",
        }
      );
      if (res.ok) {
        const updatedData = data.filter((_, i) => i !== rowIndex);
        setData(updatedData);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleFilter = (e) => setFilter(e.target.value)
  const handleClick = (i) => history.push(`/edit/${i}`)
  const getFilter = () => filter?.toLowerCase().trim()

  const displayList = data?.filter((item) => !filter || item.Name.toLowerCase().indexOf(getFilter()) >= 0 || item.Author.toLowerCase().indexOf(getFilter()) >= 0)

  return (
    <div>

      {/* mode d'affichage */}
      <div className="mb-3">
        <div className="form-check form-switch">
          <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" checked={displayMode === 'table'}
            onChange={(e) => setDisplayMode(e.target.checked ? 'table' : 'list')}
          />
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault" >
            {displayMode === 'table' ? "Affichage condensé" : "Affichage en liste"}
          </label>

        </div>
      </div>

      {/* Recherche*/}
      <div className="mb-3">
        <input type="text" className="form-control" placeholder="rechercher: un titre, un auteur" value={filter || ''} onChange={handleFilter} autoFocus />
      </div>

      {/* Mode table */}
      <table className={`table table-hover table-sm align-middle ${displayMode === 'table' && displayList?.length > 0 ? '' : 'd-none'}`}>
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col">Titre</th>
            <th scope="col"></th>
            <th scope="col">Auteur</th>
            <th scope="col">Genre</th>
          </tr>
        </thead>
        <tbody>
          {displayMode === 'table' && displayList?.map((item, i) => (
              <tr key={i} onClick={() => handleClick(item._rowNumber-2)}>
                <th scope="row">
                  <img
                    src={item.Jacket || 'logo192.png'}
                    height="50" width="30" alt='no-pic' ></img>
                </th>
                <td>{item.Name}</td>
                <td>{item.Tome ? ('tome ' + item.Tome) : ('')}</td>
                <td>{item.Author}</td>
                <td>{item.Genre}</td>
              </tr>
            ))}
        </tbody>
      </table>

      {/* mode Liste */}
      <div className={`accordion ${displayMode === 'table' ? 'd-none' : ''}`} id="accordionExample">
        {displayMode !== 'table' && displayList?.map((item, i) => (
          <div className="accordion-item" key={i}>
            <h2 className="accordion-header" id={`heading${i}`}>
              <button
                className="accordion-button"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target={`#collapse${i}`}
                aria-expanded="true"
                aria-controls={`collapse${i}`}
              >
                <img
                  src={item.Jacket || 'https://img.freepik.com/premium-vector/book-cover-transparent-background-also-includes_270215-78.jpg?w=740'}
                  height="50" width="30" alt='no-pic' ></img> {item.Name} {item.Author}
              </button>
            </h2>
            <div
              id={`collapse${i}`}
              className="accordion-collapse collapse"
              aria-labelledby={`heading${i}`}
              data-bs-parent="#accordionExample"
            >
              <div className="accordion-body">
                <div className="d-flex justify-content-between align-items-center">
                  <span>
                    <strong className="display-6">{item.Name}</strong> ---{" "}
                    {item.Author}

                  </span>
                  <span>
                    <Link to={`/edit/${i}`} style={{ textDecoration: "none" }}>
                      Edit
                    </Link>
                    <button
                      className="btn btn-sm btn-danger ms-1"
                      onClick={() => handleDelete(i)}
                    >
                      X
                    </button>
                  </span>
                </div>
                <p>{item.Description}</p>
                <img src={item.Jacket} alt={item.name} ></img>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/** message aucun livre */}
      {(displayList?.length === 0)
        ? (<h4 className="col-12 text-muted text-center">Aucun livre ne corresponds à votre recherche</h4>)
        : ('')}

    </div>
  );
};

export default Home;
