import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import api from '../api'
import Fiche from '../components/Fiche'

const Add = () => {
  const history = useHistory();
  const [data, setData] = useState(api.defaultState);
  const [lists, setList] = useState(api.defaultList);

  useEffect(() => {
    const getList = async () => setList(await api.getLists())
    getList();
  }, []);

  const handleChange = (e) =>
    setData({ ...data, [e.target.name]: e.target.value });

  const handleSubmit = async (e) => {
    e.preventDefault();
    await api.addBook(data)
    history.replace("/");
  }

  return (
    <form style={{ maxWidth: 500, margin: "auto" }} onSubmit={handleSubmit}>
      <h1 className="text-muted text-center">Add</h1>

      <Fiche props={{data, lists, handleChange}} />

      <div className="text-center">
        <button className="btn btn-primary">Add</button>
      </div>
    </form>
  );
};

export default Add;
